import Firebase from 'firebase/app';
import 'firebase/auth';
import 'firebase/database';

export class Main {
  async getUser(id) {
    return new Promise((resolve) => {
      Firebase.database().ref('users/' + id).once('value').then((resp) => {
        resolve(resp)
      });
    });
  }

  getKeywords(grade) {
    let keys = {
      "preschool": ["a", "do", "is", "our", "to", "all", "eat", "it", "said", "up", "and", "for", "like", "see", "was", "are", "get", "little", "she", "we", "at", "go", "make", "so", "what", "be", "good", "me", "that", "who", "but", "it", "she", "my", "the", "will", "can", "he", "no", "there", "with", "come", "I", "not", "they", "yes", "did", "in", "on", "this"],
      "kindergarten": ["you", "about", "day", "how", "old", "three", "after", "down", "if", "one", "two", "again", "from", "just", "or", "us", "had", "give", "know", "out", "very", "any", "had", "long", "put", "were", "as", "have", "man", "some", "when", "been", "her", "many", "take", "which", "before", "here", "much", "their", "work", "boy", "him", "new", "them", "would", "by", "his", "of", "your"],
      "first": ["an", "above", "add", "number", "should", "why", "side", "something", "sometimes", "song", "soon", "young", "start", "state", "stand", "hand", "those", "thought", "spell", "picture", "together", "told", "took", "walk", "first", "water", "called", "who", "oil", "sit", "now", "find", "way", "made", "may", "part", "over", "sound", "only", "place", "years", "live", "back", "most", "things", "name", "sentence", "think", "say", "great", "where", "help", "through", "line", "right", "too", "means", "same", "tell", "follow", "came", "want", "show", "also", "around", "form", "small", "set", "end", "does", "another", "well", "large", "must", "big", "even", "such", "because", "turn", "ask", "went", "men", "read", "need", "land", "different", "home", "use", "move", "try", "kind", "changed", "off", "easy", "friends", "got", "stop", "being", "could", "head", "words", "each", "people", "become", "sure", "door", "red", "order", "heard", "other", "idea", "then", "these", "time", "than", "look", "write", "almost", "along", "area", "away", "animal", "house", "point", "page", "letter", "mother", "answer", "found", "study", "still", "learn", "play", "air", "below", "between", "birds", "body", "book", "both", "car", "carry", "children", "city", "close", "color", "complete", "country", "cut", "saw", "school", "sea", "sun", "talk", "under", "until", "began", "begin", "group", "grow", "earth", "didn't", "hard", "has", "dog", "don't", "girl", "give", "hear", "every", "example", "eye", "eyes", "face", "far", "father", "feet", "few", "horse", "fish", "food", "four", "enough", "always", "america", "paper", "own", "questions", "problem", "plant", "piece", "real", "without", "world", "river", "room", "while", "white", "watch", "water", "usually"],
    };

    return keys[grade];
  }

  play(file) {
    this.audio = new Audio(require(`../media/${file}.mp3`));
    this.audio.play();
  }

  stopPlayer() {
    if (this.audio) {
      this.audio.pause();
      this.audio.currentTime = 0;
    }
  }

  playNext(file, duration = 1000) {
    setTimeout(() => main.play(file), duration);
  }

  objToQuery(params) {
    return Object.keys(params).map(key => key + '=' + params[key]).join('&');
  }

  queryToObj(data) {
    return JSON.parse('{"' + decodeURI(data.replace(/&/g, "\",\"").replace(/=/g, "\":\"")) + '"}');
  }

  getUid() {
    return localStorage.getItem('easy_uid');
  }

  parseGrade(value) {
    if (value === 'kindergarten') return 'Kindergarten';
    if (value === 'first') return '1st Grade';

    return 'Preschool';
  }

  async addUser(data, key) {
    return new Promise((resolve) => {
      let childKey = key || Firebase.database().ref().child('user').push().key;
      Firebase.database().ref('users/' + this.getUid() + '/children/' + childKey).set(data).then(() => {
        resolve([]);
      });
    });
  }

  async deleteChild(childKey) {
    return new Promise((resolve) => {
      Firebase.database().ref('users/' + this.getUid() + '/children/' + childKey).remove().then((resp) => {
        resolve(resp);
      });
    });
  }

  async resetKeywords(grade) {
    return new Promise((resolve) => {
      Firebase.database().ref('users/' + this.getUid() + '/points_' + grade).set({points: 0, progress: 0, total: 0}).then(() => {
        resolve();
      });
    });
  }

  async getPoints(grade) {
    return new Promise((resolve) => {
      Firebase.database().ref('users/' + this.getUid() + '/points_' + grade).once('value').then((snapshot) => {
        resolve(snapshot.val());
      });
    });
  }

  async addPoints(grade, points, length) {
    let data = await this.getPoints(grade);

    let newPoints = data ? data.points + points : points;
    let newTotal = data ? data.total + 1 : 1;
    let newProgress = Math.round(newTotal * 100 / length);

    return new Promise((resolve) => {
      Firebase.database().ref('users/' + this.getUid() + '/points_' + grade).set({
        points: newPoints,
        progress: newProgress,
        total: newTotal
      }).then(() => {
        resolve({points: newPoints, total: newTotal, progress: newProgress});
      });
    });
  }
}

export const main = new Main();



