import HomePage from './pages/home.vue';
import AuthPage from './pages/auth.vue';
import ChildAction from './pages/child-action.vue';
import Test from './pages/test.vue';
import AboutPage from './pages/about.vue';
import NotFoundPage from './pages/not-found.vue';

export default [
  {
    path: '/',
    component: HomePage,
  },
  {
    path: '/child-action/',
    component: ChildAction,
  },
  {
    path: '/child-action/:id',
    component: ChildAction,
  },
  {
    path: '/test/:data',
    component: Test,
  },
  {
    path: '/auth/',
    component: AuthPage,
  },
  {
    path: '/auth/',
    component: AuthPage,
  },
  {
    path: '/about/',
    component: AboutPage,
  },
  {
    path: '(.*)',
    component: NotFoundPage,
  }
];



