<template>
  <f7-page @page:beforein="onPageBeforeIn">
    <f7-navbar ref="navbar">
      <f7-nav-title>Easy Sightwords Pro</f7-nav-title>
      <f7-nav-right>
        <f7-link icon-if-ios="f7:info" icon-if-md="material:info" class="icon-white" v-on:click="goToAboutPage"></f7-link>
      </f7-nav-right>
    </f7-navbar>

    <f7-block-title v-if="children">Children</f7-block-title>
    <f7-list v-if="children" class="children-list">
      <f7-list-item v-for="(child, index) in children"
                    :key="index"
                    :header="parseGrade(child.grade)"
                    :link="'/test/' + objToQuery(child)"
                    :title="child.name" @swipeout:deleted="deleteChild(index)"
                    after="View" swipeout>
        <f7-swipeout-actions right>
          <f7-swipeout-button @click="editChild(index)">Edit</f7-swipeout-button>
          <f7-swipeout-button delete confirm-text="Are you sure you want to delete this item? All achievements and score will be deleted!">Delete</f7-swipeout-button>
        </f7-swipeout-actions>
      </f7-list-item>
    </f7-list>

    <f7-fab v-on:click="addChild" position="right-bottom" slot="fixed" text="Add Child" color="blue">
      <f7-icon ios="f7:add" md="material:add"></f7-icon>
    </f7-fab>
  </f7-page>
</template>
<script>
  import Firebase from 'firebase/app';
  import 'firebase/auth';
  import 'firebase/database';
  import {main} from '../service/main';

  export default {
    data: function () {
      return {
        uid: localStorage.getItem('easy_uid'),
        user: null,
        children: null,
      }
    },
    mounted() {
      this.$f7ready(() => {
        this.getUser();

        //
      })
    },
    methods: {
      onPageBeforeIn() {

      },

      getUser() {
        Firebase.database().ref('users/' + this.uid).on('value', snapshot => {
          this.user = snapshot.val();
          this.children = this.user.children || null;
        });
      },

      addChild() {
        this.$f7.views.current.router.navigate('/child-action/');
      },

      goToAboutPage() {
        this.$f7.views.current.router.navigate('/about/');
      },

      editChild(childId) {
        this.$f7.views.current.router.navigate('/child-action/' + childId);
      },

      deleteChild(childId) {
        main.deleteChild(childId).then();
      },

      parseGrade(value) {
        return main.parseGrade(value);
      },

      objToQuery(obj) {
        return main.objToQuery(obj);
      },
    }
  }
</script>



