<template>
  <f7-page @page:beforein="onPageBeforeIn">
    <f7-navbar ref="navbar" back-link="Back">
      <f7-nav-title>Add/Edit Child</f7-nav-title>
    </f7-navbar>

    <f7-card>
      <f7-list inline-labels>
        <f7-list-item>
          <f7-label>Name</f7-label>
          <f7-input type="text" name="name" :value="form.name" @input="form.name = $event.target.value" placeholder="Child name" required clear-button></f7-input>
        </f7-list-item>
        <f7-list-item>
          <f7-label>Grade</f7-label>
          <f7-input type="select" name="grade" placeholder="Please choose..." required @input="form.grade = $event.target.value" :value="form.grade">
            <option value="preschool">Preschool</option>
            <option value="kindergarten">Kindergarten</option>
            <option value="first">1st Grade</option>
          </f7-input>
        </f7-list-item>
        <f7-list-item>
          <f7-button fill v-on:click="addAction()">Add Child</f7-button>
        </f7-list-item>
      </f7-list>
    </f7-card>

  </f7-page>
</template>
<script>
  import Firebase from 'firebase/app';
  import 'firebase/auth';
  import 'firebase/database';
  import {main} from '../service/main';

  export default {
    data: function () {
      return {
        uid: localStorage.getItem('easy_uid'),
        id: this.$f7route.params.id || null,
        form: {name: '', grade: ''},
      }
    },
    mounted() {
      this.$f7ready(() => {
        this.watchChild();
      })
    },
    methods: {
      onPageBeforeIn() {

      },

      watchChild() {
        if (this.id === null) return false;

        Firebase.database().ref('users/' + this.uid + '/children/' + this.id).on('value', snapshot => {
          this.form = snapshot.val();
        });
      },

      addAction() {
        if (this.form.name.trim() === '') {
          this.$f7.dialog.alert('Name is required', 'Heads Up!');
          return false;
        }
        if (this.form.grade.trim() === '') {
          this.$f7.dialog.alert('Grade is required', 'Heads Up!');
          return false;
        }

        main.addUser(this.form, this.id).then(() => {
          this.$f7.views.current.router.navigate('/');
        });
      },

    }
  }
</script>



