<template>
  <f7-page @page:beforein="onPageBeforeIn">
    <f7-navbar ref="navbar">
      <f7-nav-left>
        <f7-link icon-f7="left" class="icon-white" v-on:click="goHome"></f7-link>
      </f7-nav-left>
      <f7-nav-title class="text-align-center">Easy Sightwords Test</f7-nav-title>
    </f7-navbar>

    <f7-progressbar v-if="keywords" :progress="progress" class="keywords-progressbar"></f7-progressbar>
    <f7-card v-if="keywords">
      <f7-card-content>
        <f7-chip class="float-right" outline :text="key + ' / ' + keywords.length" color="orange"></f7-chip>
        <h1 class="text-align-center keyword">{{ keywords[key] }}</h1>
      </f7-card-content>
      <f7-card-footer class="keyword-footer" v-if="keywords[key]">
        <img v-if="!showNext && showSpeak && !showStar" class="speak-image" v-on:click="sayWord" :src="images.speak">
        <img v-if="showStar" :class="starClass" :src="images.cat">
        <img v-if="showNext" v-on:click="nextWord" :src="images.skip">
        <img v-if="showNext" v-on:click="addPoints" :src="images.next">
      </f7-card-footer>
      <f7-card-footer class="keyword-footer" v-if="!keywords[key]">
        <img class="animated jackInTheBox speak-image" v-on:click="goHome" :src="images.cat">
      </f7-card-footer>
    </f7-card>
    <f7-fab v-on:click="resetKeywords" position="right-bottom" slot="fixed" text="Reset" color="orange">
      <f7-icon ios="f7:add" md="material:add"></f7-icon>
    </f7-fab>
  </f7-page>
</template>
<script>
  import 'firebase/auth';
  import 'firebase/database';
  import {main} from '../service/main';

  export default {
    data: function () {
      return {
        uid: localStorage.getItem('easy_uid'),
        data: main.queryToObj(this.$f7route.params.data),
        keywords: null,
        key: 0,
        points: 0,
        progress: 0,
        showNext: false,
        showStar: false,
        starClass: 'animated jackInTheBox speak-image',
        showSpeak: true,
        audio: null,
        images: {
          cat: require(`../images/cat.png`),
          next: require(`../images/check.png`),
          skip: require(`../images/skip.png`),
          speak: require(`../images/speak.png`)
        },
      }
    },
    mounted() {
      this.$f7ready(() => {
        this.keywords = main.getKeywords(this.data.grade);

        this.initialStartVoice();

        this.$f7.dialog.preloader();
        main.getPoints(this.data.grade).then((data) => {
          if (data) {
            this.points = data.points;
            this.progress = data.progress;
            this.key = data.total;
          }

          this.$f7.dialog.close();
        });
      });
    },
    methods: {
      onPageBeforeIn() {

      },

      initialStartVoice() {
        if (this.key === 0) {
          main.stopPlayer();
          main.play('_start');
        }
      },

      sayWord() {
        this.showSpeak = false;

        main.stopPlayer();

        main.play('_did-you-say');
        main.playNext(this.keywords[this.key]);

        setTimeout(() => {
          this.showNext = true;

          main.play('_continue');
        }, 2000);
      },

      addPoints() {
        main.stopPlayer();

        this.triggerStar();

        this.$f7.dialog.preloader();
        main.addPoints(this.data.grade, 5, this.keywords.length).then((data) => {
          if (data) {
            this.points = data.points;
            this.progress = data.progress;
          }

          this.$f7.dialog.close();

          this.nextWord();
        });
      },

      triggerStar() {
        this.starClass = 'animated jackInTheBox speak-image';
        this.showStar = true;
        setTimeout(() => {
          this.starClass = 'animated zoomOut speak-image';
          this.showStar = false;
        }, 2000);
      },

      retryWord() {
        this.showNext = false;
        this.showSpeak = true;
      },

      nextWord() {
        main.stopPlayer();

        this.showNext = false;
        this.showSpeak = true;

        if (this.keywords[this.key + 1] === undefined) {
          main.play('_finish');
        }

        this.key = this.key + 1;
      },

      resetKeywords() {
        this.$f7.dialog.preloader();
        main.resetKeywords(this.data.grade).then(() => {
          this.$f7.dialog.close();

          this.goHome();
        });
      },

      goHome() {
        main.stopPlayer();
        this.$f7.views.current.router.navigate('/');
      }
    }
  }
</script>



