<template>
  <f7-page>
    <f7-navbar ref="navbar">
      <f7-nav-left>
        <f7-link icon-if-ios="f7:home" icon-if-md="material:home" class="icon-white" v-on:click="goToHomePage"></f7-link>
      </f7-nav-left>
      <f7-nav-title>About Easy Sightwords</f7-nav-title>
    </f7-navbar>

    <f7-block-title>Pro version</f7-block-title>
    <f7-card>
      <f7-card-content>
        <p>This is the Pro version of the app, with over 200 words for ages Preschool through 1st Grade.</p>
        <p>Sight words made easy. This app has over 200 sightwords for ages Preschool through 1st Grade.</p>
        <p>Sightwords can be practiced as flashcards where kids can earn points for guessing the correct word.</p>
        <p>Encourage your child to test themselves with sight word practice and give them rewards (maybe an ice cream or sticker) correctly guessed words.</p>
        <p>App allows the parent to create individual profiles for each child in the family.</p>
        <ul>
          <li>Preschool</li>
          <li>Kindergarten</li>
          <li>1st Grade</li>
        </ul>
        <p>If you need any new words added, leave a review in the app.</p>
      </f7-card-content>
    </f7-card>

    <f7-block-title>Keywords</f7-block-title>
    <f7-card>
      <f7-card-content>
        <p>Preschool</p>
        <ul>
          <li v-for="(key, index) in preschool" :key="index">{{key}}</li>
        </ul>
        <p>Kindergarten</p>
        <ul>
          <li v-for="(key, index) in kindergarten" :key="index">{{key}}</li>
        </ul>
        <p>1st Grade</p>
        <ul>
          <li v-for="(key, index) in first" :key="index">{{key}}</li>
        </ul>
      </f7-card-content>
    </f7-card>
  </f7-page>
</template>

<script>
  import {main} from '../service/main';

  export default {
    data: function () {
      return {
        preschool: main.getKeywords('preschool'),
        kindergarten: main.getKeywords('kindergarten'),
        first: main.getKeywords('first'),
      }
    },
    methods: {
      goToHomePage() {
        this.$f7.views.current.router.navigate('/');
      },
    }
  }
</script>



